<template>
    <div class="mBg">
        <mhead-top titleHead="订单详情" lfIcon="el-icon-arrow-left icon" lfUrl="/userCenter/order/order" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mo-state">
            <div class="mo-state-card">
                <h1>{{orderData.state}}</h1>
                <img src="">
            </div>
        </div>
        <div class="mo-distrib pr" v-if="orderData.logistics">
            <div class="mo-di-line" @click="toLogistics(order_id)">
                <i class="el-icon-truck icon"></i>
                <div class="mo-di-info">
                    <div class="text">{{orderData.logistics.code==12010?'包裹正在等待揽收':'运送中'}}</div>
                    <div class="date" v-if="orderData.logistics.code!=12010">{{orderData.logistics.data[0].brand}} 单号：{{orderData.logistics.data[0].no}}</div>
                </div>
            </div>
            <i class="el-icon-arrow-right icon arrowRight"></i>
        </div>
        <div class="mo-rece" v-if="orderData.address!=''">
            <div class="mo-di-line">
                <i class="el-icon-location-information icon"></i>
                <div class="mo-di-info w">
                    <div class="flex f-nowrap j-bet">
                        <span>{{orderData.username}}</span>
                        <span>{{orderData.mobile}}</span>
                    </div>
                    <div class="color9">{{orderData.address}}</div>
                </div>
            </div>
        </div>
        <div class="mo-shopList">
            <h1>商品清单</h1>
            <div class="mop-item" v-for="(item,index) in orderData.goods" @click="toGoodDetail(item.spec_id)">
                <div class="mop-pic">
                    <el-image class="mop-image" :src="item.pic"></el-image>
                </div>
                <div class="mop-info">
                    <div class="mod-name">{{item.name}}</div>
                    <div class="mod-spec">
                        <span>规格：{{item.spec_name}}{{item.spec_unit}}</span>
                    </div>
                    <div class="mod-price">
                        <div class="mop-price">￥{{item.price}}/{{item.supply_unit}}</div>
                        <div class="mop-number">
                            ×{{item.number}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
<!--        <div class="mo-shopItem">-->
<!--            <div class="mo-item-line">-->
<!--                <div class="mo-tit">订单留言</div>-->
<!--                <div class="mo-text">djfkjfkdkfdksf</div>-->
<!--            </div>-->
<!--        </div>-->
        <div class="mo-shopItem" v-if="orderData.invoice">
            <div v-if="orderData.invoice.type==0">
                <!--0是企业-->
                <div class="mo-item-line">
                    <div class="mo-tit">发票类型</div>
                    <div class="mo-text">企业</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">发票抬头</div>
                    <div class="mo-text">{{orderData.invoice.ename}}</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">企业税号</div>
                    <div class="mo-text">{{orderData.invoice.taxnumber}}</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">开户银行</div>
                    <div class="mo-text">{{orderData.invoice.bank}}</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">银行账号</div>
                    <div class="mo-text">{{orderData.invoice.banknumber}}</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">地址</div>
                    <div class="mo-text">{{orderData.invoice.address}}</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">电话</div>
                    <div class="mo-text">{{orderData.invoice.phone}}</div>
                </div>
            </div>
            <div v-else>
                <div class="mo-item-line">
                    <div class="mo-tit">发票类型</div>
                    <div class="mo-text">个人</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">发票抬头</div>
                    <div class="mo-text">{{orderData.invoice.title}}</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">地址</div>
                    <div class="mo-text">{{orderData.invoice.address}}</div>
                </div>
                <div class="mo-item-line">
                    <div class="mo-tit">电话</div>
                    <div class="mo-text">{{orderData.invoice.phone}}</div>
                </div>
            </div>
        </div>
        <div class="mo-shopItem">
            <div class="mo-item-line">
                <div class="mo-tit">商品总额</div>
                <div class="mo-text">￥{{orderData.price_total}}</div>
            </div>
            <div class="mo-item-line">
                <div class="mo-tit">订单总价</div>
                <div class="mo-text">￥{{orderData.price_total}}</div>
            </div>
            <div class="mo-item-line count">
                <div class="mo-tit">实付金额</div>
                <div class="mo-text">￥{{orderData.price_total}}</div>
            </div>
        </div>
        <div class="mo-shopItem">
            <div class="mo-item-line">
                <div class="mo-tit">订单编号</div>
                <div class="mo-text">{{orderData.order_code}}</div>
            </div>
            <div class="mo-item-line">
                <div class="mo-tit">下单时间</div>
                <div class="mo-text">{{orderData.time}}</div>
            </div>
        </div>
        <div class="mo-foot">
            <div class="mo-foot-box">
                <el-button class="btn" size="small" @click="cancelOrder(order_id)" v-if="orderData.state=='待付款'">取消订单</el-button>
                <el-button class="btn w" size="small" type="primary" v-if="orderData.state=='配送中'||orderData.state=='已付款'" @click="orderConfirm(order_id)">确认收货</el-button>
                <el-button class="btn" size="small" v-if="orderData.state=='已签收'&&orderData.is_comment==0" plain type="primary" @click="toEvaluate(order_id)">评价</el-button>
                <span v-if="orderData.state=='已签收'&&orderData.is_comment==1" style="color:#2DBE60;"><i class="el-icon-success mr10"></i>订单已完成，您已评价该商品。</span>
                <el-button class="btn" size="small" v-if="orderData.state=='待付款'" type="warning" plain @click="payNow(order_id)">立即付款</el-button>
                <el-button class="btn" size="small" v-if="orderData.state=='取消订单'" type="info" disabled plain>订单已取消</el-button>
            </div>
        </div>
    </div>
</template>


<script>
    import mheadTop from '../../../components/mobile/headTop';
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {getOrderInfo,orderCancel,orderConfirm,getOpenIdToWx} from '../../../service/api';
    export default {
        name: 'mOrderDetail',
        data(){
            return{
                order_id:'',
                orderData:{
                    invoice:{
                        invoice_id:'',
                        type:''
                    }
                }
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),
        },
        methods:{
            async initData(){
                this.order_id = this.$route.query.order_id || '';
                var res =  await getOrderInfo(this.order_id);
                if(res.status == 12007){
                    this.$alert('立即前往登录/注册', '您还未登录', {
                        confirmButtonText: '确定',
                        callback: action => {
                            this.$router.push({path:"/login/login"});
                        }
                    });
                }else{
                    this.orderData = res;
                }
            },
            async cancelOrder(order_id){
                this.$confirm('你确定要取消该订单吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await orderCancel(order_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {
                });
            },
            async toLogistics(order_id){
                if(this.orderData.logistics.code==0){
                    this.$router.push({path:"/userCenter/order/logistics",query:{order_id:order_id}});
                }else{
                    this.$message({
                        message:'暂无更多物流信息',
                        type:'warning'
                    });
                }
            },
            toEvaluate(order_id){
                this.$router.push({path:"/userCenter/evaluate/evaluate",query:{order_id:order_id}});
            },
            async payNow(orderId){
                var ua = window.navigator.userAgent.toLowerCase();
            　　if (ua.match(/MicroMessenger/i) == 'micromessenger'){
                    //是微信浏览器
                    var tar = await getOpenIdToWx(orderId);
                    window.location.href=tar.url;
            　　}else{
                    this.$router.push({path:"/order/pay",query:{orderId:orderId}});
                }
            },
            orderConfirm(order_id){
                this.$confirm('是否确认收货?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await orderConfirm(order_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {
                });
            },
            toGoodDetail(spec_id){
                this.$router.push({path:"/good/detail",query:{specifications_id:spec_id}});
            }
        },
        created(){
            //this.initData();
        },
        watch:{
            userInfo: function (value) {
                if (value && value.user_id) {
                    this.initData();
                }
            },
            $route(){
                this.order_id = this.$route.query.order_id || '';
                this.orderData =  getOrderInfo(this.order_id);
            }
        }

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    /*状态卡*/
    .mo-state{
        padding:px2rem(20);
    }
    .mo-state-card{
        background-color: $default;
        color:#fff;
        padding:px2rem(20);
        font-size: px2rem(32);
        border-radius: px2rem(10);
        height: px2rem(100);
    }
    /*物流*/
    .mo-distrib{
        background-color: #fff;
        padding:px2rem(20);
        margin-bottom: px2rem(20);
    }
    .mo-di-line{
        display: flex;
        flex-wrap: nowrap;
    }
    .mo-di-line .icon{
        font-size: px2rem(40);
        color: #999;
        padding-top: px2rem(8);
        width:px2rem(60);
    }
    .mo-di-info .text{
        color:$default;
    }
    .mo-di-info .date{
        color:#999;
        font-size: px2rem(26);
    }
    .arrowRight{
        position: absolute;
        right:px2rem(20);
        top:px2rem(30);
        font-size:px2rem(40);
        color:#999;
    }
    /*收货地址*/
    .mo-rece{
        background-color: #fff;
        padding:px2rem(20);
        margin-bottom: px2rem(20);
    }
    /*商品*/
    .mo-shopList{
        background-color: #fff;
        padding:px2rem(20);
        margin-bottom: px2rem(20);
    }
    .mop-item{
        background: #f5f5f5;
        padding:px2rem(10);
        margin-top:px2rem(20);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
    }
    .mop-pic{
        width:px2rem(110);
        height: px2rem(110);
        padding-right:px2rem(20);
    }
    .mop-image{
        width:px2rem(110);
        height: px2rem(110);
        object-fit: contain;
        background-color: #f2f2f2;
    }
    .mop-info{
        width:px2rem(510);
    }
    .mod-name{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        color:#333;
        line-height: px2rem(36);
        min-width: px2rem(460);
    }
    .mod-spec{
        font-size:px2rem(24);
        color:#999;
        display: block;
        height: px2rem(36);
        line-height: px2rem(36);
    }
    .mod-spec span{
        display: inline-block;
        float:left;
    }
    .mod-spec .icon{
        font-size:px2rem(22);
        color:#999;
        height: px2rem(36);
        line-height: px2rem(36);
        padding-left:px2rem(6);
        float:left;
    }
    .mod-price{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    /*内容*/
    .mo-shopItem{
        background-color: #fff;
        padding:px2rem(20);
        margin-bottom: px2rem(20);
    }
    .mo-item-line{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .mo-tit{
        width:px2rem(150);
    }
    .mo-text{
        width:px2rem(560);
        text-align: right;
    }
    .mo-item-line.count{
        border-top:1px solid #ddd;
        margin-top:px2rem(20);
        padding-top:px2rem(20);
    }
    .mo-item-line.count .mo-tit{
        font-weight:bold;
    }
    .mo-item-line.count .mo-text{
        color:$colorprice
    }
    /*底部*/
    .mo-foot{
        width:100%;
        height: px2rem(104);
        position: fixed;
        bottom:0;
        left:0;
        background-color:$colorwhite;
        border-top:1px solid #eee;
        z-index: 1000;
        text-align: right;
    }
    .mo-foot-box{
        line-height: px2rem(104);
        padding:0 px2rem(20);
    }
    .mo-foot .btn{
        margin-top:px2rem(10);
        font-size: px2rem(28);
    }


</style>



